@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Playfair Display", serif;
}
/* Backgrounds */ 
.dark-brown-background{
  background-color: #000000 ;
}

.light-brown-background{
  background-color: #A9A9A9;
}
.dark-beige-background{
  background-color: #ffffff;
}

.light-beige-background{
  background-color: #FFFFFF;
}
/* Texts */ 
.dark-brown-text{
  color: #000000;
}

.light-brown-text{
  color: #000000;
}

.dark-beige-text{
  color: #D3D3D3;
}

.light-beige-text{
  color: #FFFFFF;
}
/* hover texts */ 
.hver-dark-brown:hover{
  color: #000000 ;
}

.hver-light-brown:hover{
  color:#A9A9A9;
}

.hver-beige:hover{
  color: #D3D3D3;
}
.hver-light-beige:hover{
  color: #FFFFFF;
}
/*SVG Handeling*/
.svg:hover{
  fill: #D3D3D3;
}
/* hover backgrounds */ 
.hver-beige-bg:hover{
  background-color: #D3D3D3;
}
.hver-dark-brown-bg:hover{
  background-color: #000000 ;
}
/* beige border */
.beige-border{
  border: 2px solid #D3D3D3;
}
/* hide scroll bar */ 
.hide-scroll-bar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.hide-scroll-bar::-webkit-scrollbar {
  display: none;
}
/*scroll buttons*/
.horizontal-scroll-buttons{
  position: relative;
  top: -175px;
}
/*boder on hover*/
.hver-border-dark-beige:hover{
  border: 2px solid #D3D3D3;
}
